<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <table class="table tableText tableSm table-bordered border-primary m-0">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">
          {{ $t('shoppingOptions.product') }}
        </th>
        <th scope="col">{{ $t('shoppingOptions.size') }}</th>
        <th scope="col">
          {{ $t('shoppingOptions.price') }}
        </th>
        <th scope="col">{{ $t('shoppingOptions.qty') }}</th>
        <th scope="col">{{ $t('shoppingOptions.sub') }}</th>
      </tr>
    </thead>
    <tbody class="table-group-divider luluText">
      <template v-for="(item, index) in cookieList" :key="index">
        <tr>
          <td>{{ index + 1 }}</td>
          <td>
            <div class="fw-bolder">{{ $t(item.flavor) }}</div>
            <p
              class="mb-0"
              v-if="item.type === 'drink' || item.type === 'coffee'"
            >
              {{ $t('shoppingOptions.drink.ice') }}:<span
                class="fst-italic text-secondary"
                >{{ $t(iceList[item.ice].title) }}</span
              >, {{ $t('shoppingOptions.drink.sugar') }}:<span
                class="fst-italic text-secondary"
                >{{ $t(sugarList[item.sugar].title) }}</span
              >
            </p>
          </td>
          <td>{{ item.size }}</td>
          <td>${{ item.price }}</td>
          <td>
            <nav aria-label="..." class="me-4">
              <ul class="pagination mb-0 align-items-center">
                <li class="page-item">
                  <a
                    class="page-link rounded-0"
                    href="#"
                    @click.prevent="changeQty(index, -1)"
                  >
                    -
                  </a>
                </li>
                <li class="">
                  <div class="mx-2">{{ item.qty }}</div>
                </li>
                <li class="page-item">
                  <a
                    class="page-link rounded-0"
                    href="#"
                    @click.prevent="changeQty(index, 1)"
                  >
                    <i class="bi bi-plus-lg"></i>
                  </a>
                </li>
              </ul>
            </nav>
          </td>
          <td>
            $<span class="fw-bold text-secondary">{{
              item.price * item.qty
            }}</span>
          </td>
        </tr>
      </template>
      <tr>
        <td colspan="6">
          <h4 class="text-end">
            {{ $t('shoppingOptions.total') }}：
            <span class="fw-bold text-secondary"
              >${{ $toCurrency(total) }}</span
            >
          </h4>
        </td>
      </tr>
      <tr v-show="cookieList.length !== 0">
        <td colspan="6" class="text-end">
          <a href="#" @click.prevent="createOrder">
            <component :is="'wired-button'" class="bg-dark text-light h4">
              <i class="bi bi-caret-right-fill text-light px-2"></i
              >{{ $t('shoppingOptions.checkout') }}
            </component>
          </a>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import Cookies from 'js-cookie';
import { apiPostOrder } from '@/assets/javascript/api';
import iceOption from '@/assets/json/iceOption.json';
import sugarOption from '@/assets/json/sugarOption.json';

export default {
  data() {
    return {
      total: 0,
      cookieList: [],
      iceList: iceOption,
      sugarList: sugarOption,
    };
  },
  props: ['cookieData', 'productList'],
  watch: {
    cookieData() {
      this.cookieList = JSON.parse(JSON.stringify(this.cookieData));
      this.totalPrice();
    },
  },
  methods: {
    changeQty(index, num) {
      this.cookieList[index].qty += num;
      if (this.cookieList[index].qty === 0 && this.cookieList.length === 1) {
        this.cookieList.splice(index, 1);
        Cookies.remove('LULUchartList');
        this.total = 0;
      } else if (this.cookieList[index].qty === 0) {
        this.cookieList.splice(index, 1);
        this.tempCookie = '';
        this.cookieList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      } else {
        this.tempCookie = '';
        this.cookieList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      }
      this.$emitter.emit('getCookie');
    },
    totalPrice() {
      this.total = 0;
      this.cookieList.forEach((item) => {
        this.total += item.price * item.qty;
      });
    },
    createOrder() {
      apiPostOrder(this.cookieList)
        .then((res) => {
          if (Cookies.get('luluBakeOrder') === undefined) {
            Cookies.set(
              'luluBakeOrder',
              JSON.stringify({
                // eslint-disable-next-line no-underscore-dangle
                orderId: res.data.data._id,
                num: res.data.data.productList.length,
                cookieId: new Date().getTime(),
              }),
            );
          } else {
            let currentItem = Cookies.get('luluBakeOrder');
            // eslint-disable-next-line no-underscore-dangle
            currentItem = `${currentItem}; ${JSON.stringify({
              // eslint-disable-next-line no-underscore-dangle
              orderId: res.data.data._id,
              num: res.data.data.productList.length,
              cookieId: new Date().getTime(),
            })}`;
            Cookies.set('luluBakeOrder', currentItem);
          }
          Cookies.remove('LULUchartList');
          // eslint-disable-next-line no-underscore-dangle
          this.$router.push(`/order/${res.data.data._id}`);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
