<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section>
    <!-- type -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.type') }}:</h4>
      <template v-for="(item, index) in types" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-type"
          :id="`type${index}`"
          autocomplete="off"
          :value="item.category"
          @click="(option.qty = 1), slelectProduct(item.category)"
        />
        <label
          class="btn border-0 position-relative me-4"
          :for="`type${index}`"
        >
          {{ $t(item.title) }}
          <CircleTwo
            :class="{ 'd-none': tempProduct.category !== item.category }"
          />
        </label>
      </template>
    </div>
    <!-- flavor -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.flavor') }}:</h4>
      <template v-for="(item, index) in tempProductList" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-flavor"
          :id="`flavor${index}`"
          autocomplete="off"
          :value="item.title"
          v-model="option.flavor"
          @click="
            (option.qty = 1),
              (tempProduct = item),
              (option.optionId = tempProduct.options[0]._id),
              (option.price = tempProduct.options[0].price),
              (option.size = tempProduct.options[0].size),
              (option.stock = tempProduct.options[0].stock)
          "
        />
        <label
          class="btn border-0 position-relative me-3"
          :for="`flavor${index}`"
        >
          {{ $t(item.title) }}
          <CircleOne :class="{ 'd-none': item.title !== option.flavor }" />
        </label>
      </template>
    </div>
    <!-- size -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.size') }}:</h4>
      <template v-for="(item, index) in tempProduct.options" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-type"
          :id="`size${index}`"
          autocomplete="off"
          :value="item.size"
          v-model="option.size"
          @click="
            (option.price = item.price),
              (option.qty = 1),
              (option.stock = item.stock)
          "
        />
        <label
          class="btn border-0 position-relative me-3"
          :for="`size${index}`"
        >
          {{ item.size }}
          <CircleTwo :class="{ 'd-none': option.size !== item.size }" />
        </label>
      </template>
    </div>
    <!-- typeof drink -->
    <section
      v-if="
        tempProduct.category === 'drink' || tempProduct.category === 'coffee'
      "
    >
      <!-- ice -->
      <div class="mb-4">
        <h4>{{ $t('shoppingOptions.drink.ice') }}:</h4>
        <template v-for="(item, index) in iceList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`ice${index}`"
            autocomplete="off"
            :value="index"
            v-model="option.ice"
          />
          <label
            class="btn border-0 position-relative me-3"
            :for="`ice${index}`"
          >
            {{ $t(item.title) }}
            <CircleThree :class="{ 'd-none': index !== option.ice }" />
          </label>
        </template>
      </div>
      <div class="mb-4">
        <h4>{{ $t('shoppingOptions.drink.sugar') }}:</h4>
        <template v-for="(item, index) in sugarList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`sugar${index}`"
            autocomplete="off"
            :value="index"
            v-model="option.sugar"
          />
          <label
            class="btn border-0 position-relative me-3"
            :for="`sugar${index}`"
          >
            {{ $t(item.title) }}
            <CircleTwo :class="{ 'd-none': index !== option.sugar }" />
          </label>
        </template>
      </div>
    </section>
    <!-- price -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.price') }}:</h4>
      <p class="ms-3">
        $
        <span class="text-secondary">{{ option.price }}</span>
        / {{ $t('shoppingOptions.each') }}
      </p>
    </div>
    <!-- num -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.qty') }}:</h4>
      <nav aria-label="..." class="me-4">
        <ul class="pagination">
          <li class="page-item">
            <a
              class="page-link rounded-0"
              href="#"
              @click.prevent="option.qty--"
              :class="{ disabled: option.qty <= 1 }"
            >
              -
            </a>
          </li>
          <li class="page-item">
            <div class="page-link">{{ option.qty }}</div>
          </li>
          <li class="page-item">
            <a
              class="page-link rounded-0"
              href="#"
              @click.prevent="option.qty++"
              :class="{ disabled: option.qty >= option.stock }"
            >
              <i class="bi bi-plus-lg"></i>
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <!-- 總金額 -->
    <div class="mb-4">
      <h4 class="text-uppercase">{{ $t('shoppingOptions.total') }}：</h4>
      <div class="fst-italic ms-3 h4 text-secondary">
        ${{ this.$toCurrency(option.price * option.qty) }}
      </div>
    </div>
    <component
      :is="'wired-button'"
      class="bg-dark text-light h4 mb-0"
      @click="addChart"
    >
      {{ $t('shoppingOptions.addToChart')
      }}<i class="bi bi-plus-lg ms-2 text-light"></i>
    </component>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 5rem;
}
.circle-2 {
  width: 5rem;
}
.circle-3 {
  width: 5rem;
}
</style>

<script>
import sugarOption from '@/assets/json/sugarOption.json';
import iceOption from '@/assets/json/iceOption.json';
import CircleOne from '../helpers/focus/CircleOne.vue';
import CircleTwo from '../helpers/focus/CircleTwo.vue';
import CircleThree from '../helpers/focus/CircleThree.vue';
import 'wired-elements';

export default {
  data() {
    return {
      tempProduct: {},
      tempProductList: [],
      types: [
        { title: 'shoppingOptions.typeList.cake', category: 'cake' },
        { title: 'shoppingOptions.typeList.drink', category: 'drink' },
        { title: 'shoppingOptions.typeList.coffee', category: 'coffee' },
        { title: 'shoppingOptions.typeList.meal', category: 'meal' },
      ],
      iceList: iceOption,
      sugarList: sugarOption,
      option: {
        productId: '',
        optionId: '',
        type: '',
        flavor: '',
        ice: 0,
        sugar: 0,
        size: '',
        qty: 1,
        stock: 0,
      },
    };
  },
  props: ['productList'],
  watch: {
    productList() {
      this.slelectProduct();
    },
    tempProduct() {
      this.$emitter.emit('productData', this.tempProduct);
    },
  },
  components: { CircleOne, CircleTwo, CircleThree },
  methods: {
    addChart() {
      this.$emitter.emit('addCookieChart', this.option);
    },
    slelectProduct(type = 'cake') {
      this.tempProductList = [];
      this.productList.forEach((item) => {
        if (item.category === type) {
          this.tempProductList.push(item);
        }
      });
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.tempProductList[0];
      this.option = {
        flavor: this.tempProduct.title,
        ice: 0,
        sugar: 0,
        size: this.tempProduct.options[0].size,
        // eslint-disable-next-line no-underscore-dangle
        productId: this.tempProduct._id,
        // eslint-disable-next-line no-underscore-dangle
        optionId: this.tempProduct.options[0]._id,
        price: this.tempProduct.options[0].price,
        stock: this.tempProduct.options[0].stock,
        qty: 1,
        type,
      };
    },
  },
};
</script>
